import React from "react";
import { Link } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { LOGOUTUSER } from "../redux/action";
import { useHistory } from "react-router";
const Header = () => {
  const dispatch = useDispatch();
  const history = useHistory();
  const currentUser = useSelector(({ user }) => user.currentUser);
  const handleLogout = () => {
    history.push({
      pathname: "/",
      state: { message: "You have been logout" },
    });
    dispatch(LOGOUTUSER());
  };
  return (
    <header className="nav-on-top">
      <div
        id="header"
        className="nav-header header-four bg-secondary py-10 border-bottom-1-dark"
      >
        <div className="container">
          <div className="row">
            <div className="col-lg-12">
              <nav className="navbar navbar-expand-lg navbar-light px-0">
                <a className="navbar-brand" href="/">
                  <img
                    className="nav-logo"
                    src="images/logo/logo2.png"
                    alt="logo"
                  />
                </a>
                <button
                  className="toggle-btn"
                  data-toggle="collapse"
                  data-target="#navbarSupportedContent"
                  aria-controls="navbarSupportedContent"
                  aria-expanded="false"
                  aria-label="Toggle navigation"
                >
                  <span />
                  <span />
                  <span />
                </button>
                <div
                  className="collapse navbar-collapse"
                  id="navbarSupportedContent"
                >
                  <ul className="navbar-nav mx-auto">
                    <li className="nav-item dropdown">
                      <a
                        className="nav-link dropdown-toggle"
                        href="#"
                        role="button"
                        data-toggle="dropdown"
                        aria-haspopup="true"
                        aria-expanded="false"
                      >
                        Location
                      </a>
                      <ul className="dropdown-menu shadow">
                        <li>
                          <a className="dropdown-item">Nigeria</a>
                        </li>
                        <li>
                          <a className="dropdown-item">Kenya</a>
                        </li>
                        <li>
                          <a className="dropdown-item">Ireland</a>
                        </li>
                        <li>
                          <a className="dropdown-item">Usa</a>
                        </li>
                      </ul>
                    </li>
                    <li className="nav-item dropdown">
                      <a
                        className="nav-link dropdown-toggle"
                        href="#"
                        role="button"
                        data-toggle="dropdown"
                        aria-haspopup="true"
                        aria-expanded="false"
                      >
                        Contacts
                      </a>
                    </li>
                    <li className="nav-item dropdown">
                      <a
                        className="nav-link dropdown-toggle"
                        href="#"
                        role="button"
                        data-toggle="dropdown"
                        aria-haspopup="true"
                        aria-expanded="false"
                      >
                        About us
                      </a>
                    </li>
                    <li className="nav-item dropdown">
                      <a
                        className="nav-link dropdown-toggle"
                        href="#"
                        role="button"
                        data-toggle="dropdown"
                        aria-haspopup="true"
                        aria-expanded="false"
                      >
                        FAQ
                      </a>
                    </li>

                    {/* <li className="nav-item">
                      <form className="search-field">
                        <input
                          type="search"
                          className="search form-control"
                          placeholder="Search Here"
                        />
                        <span>
                          <i className="fa fa-search" />
                          <i className="fa fa-times" />
                        </span>
                      </form>
                    </li> */}
                  </ul>
                  {currentUser ? (
                    <ul className="admin-info color-white-a">
                      <li>
                        <Link to="">{currentUser.user.firstName}</Link>
                      </li>
                      <li>
                        <a onClick={handleLogout}>Log Out</a>
                      </li>
                    </ul>
                  ) : (
                    <ul className="admin-info color-white-a">
                      <li>
                        <Link to="/login">Sign In</Link>
                      </li>
                      <li>
                        <Link to="/register">Sign Up</Link>
                      </li>
                    </ul>
                  )}
                </div>
              </nav>
            </div>
          </div>
        </div>
      </div>
      <div className="header-src-fild bg-secondary py-5">
        <div className="container">
          <ul className="row color-white-a subnavuUl">
            <li>
              <Link to="postadd">Post Ad</Link>
            </li>
            <li>
              <Link to="/Browse-rooms">
                <a>Browse</a>
              </Link>
            </li>
            <li>
              <a>Accounts</a>
            </li>
          </ul>
        </div>
      </div>
    </header>
  );
};

export default Header;
